import React from 'react';
import styled from 'styled-components';

const StyledInt = styled.div`
    text-align: center;
    p {
        max-width: 550px;
    }
`

const Introduction = () => {
    return (
        <StyledInt>
            <h1>Hi, My name is Adrian Serbanescu</h1>
            <p>
                I'm continually learning and building software.
                <br/>At the moment I'm focused on web development with  React, Express.js &amp; TypeScript.
            </p>
            <p>
                You can ckeck-up my work at <a href="https://github.com/adrianaris">github</a>.
            </p>
        </StyledInt>
    )
};

export default Introduction;
